import Main from './views/Main.vue'
import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Table from './views/nav1/Table.vue'
import Alarm from './views/nav2/Alarm.vue'
import Journal from './views/nav2/Journal.vue'
import Role from './views/nav1/Role.vue'
import Resource from './views/nav1/Resource.vue'
import Echarts from './views/charts/Echarts.vue'
import Refresh from './views/Refresh.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/refresh',
        component: Refresh,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/main',
        component: Main,
        iconCls:"fa fa-home",
        name: '主页'},
    {
        path: '/',
        component: Home,
        name: '人员管理',
        iconCls: 'fa fa-user',//图标样式class
        children: [
            { path: '/table', component: Table, name: '用户管理' },
            { path: '/role', component: Role, name: '角色管理' },
            { path: '/resource', component: Resource, name: '资源管理' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '告警管理',
        iconCls: 'fa fa-bell',
        children: [
            { path: '/alarm', component: Alarm, name: '告警查看' },
            { path: '/journal', component: Journal, name: '告警日志' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'Charts',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/echarts', component: Echarts, name: 'Echarts.vue' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;



